import Konva from 'konva';
/**
 * 
 */
class Mask {
    constructor(width, height) {
        this.width = width;
        this.height = height;
        return new Konva.Rect({
            width: this.width,
            height: this.height,
            fill: 'black',
            stroke: 'black',
            opacity: 0.6,
            visible: false, //
            draggable: false, //
        });
    };
}

export default Mask;


